.container {
  flex-grow: 1;
  margin: 0 auto;
  position: relative;
  width: auto;
}

.container.is-fluid {
  max-width: none !important;
  padding-left: 32px;
  padding-right: 32px;
  width: 100%;
}

/* Mobile Phones */
@media (min-width: 360px) {
  .container {
    max-width: 320px;
  }
}

/* Larger Mobile Phones, Small Tablets */
@media (min-width: 640px) {
  .container {
    max-width: 576px;
  }
}

/* Tablets and Small Laptops */
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

/* Medium-sized Laptops and Desktops */
@media (min-width: 1024px) {
  .container {
    max-width: 960px;
  }
}

/* Large Laptops and Desktops */
@media (min-width: 1280px) {
  .container {
    max-width: 1152px;
  }
}

/* Extra Large Screens */
@media (min-width: 1536px) {
  .container {
    max-width: 1344px;
  }
}

/* Ultra HD Monitors (4K) */
@media (min-width: 1920px) {
  .container {
    max-width: 1600px;
  }
}

/* Ultra HD Monitors (5K) */
@media (min-width: 2560px) {
  .container {
    max-width: 2160px;
  }
}

/* Ultra HD Monitors (8K) */
@media (min-width: 3840px) {
  .container {
    max-width: 2880px;
  }
}

/* Ultra Wide Monitors (e.g., 21:9 aspect ratio) */
@media (min-width: 5120px) {
  .container {
    max-width: 3840px;
  }
}

/* Very Large Screens (e.g., 8K Ultra Wide Monitors) */
@media (min-width: 7680px) {
  .container {
    max-width: 5760px;
  }
}

/* Ultra Wide Monitors (e.g., 21:9 aspect ratio) */
@media (min-width: 3440px) {
  .container {
    max-width: 2880px;
  }
}

.text.container {
  font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
  max-width: 700px !important;
  line-height: 1.5;
  font-size: 1rem;
}

